.App {
    text-align: center;
  }
  
  .HomePage {
    padding: 50px;
  }
  
  .search_bar {
    margin-top: 20px;
  }
  
  input {
    padding: 5px;
    margin-right: 10px;
  }
  
  button {
    padding: 5px 15px;
  }
  body {
    background-color: #333; /* Dark grey background */
    color: white; /* White text color */
  }
  
  .button-style {
    background-color: #444;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    margin-right: 10px;
    transition: transform 0.2s ease; /* Add transition for transform property */
  }
  
  /* Add a style for selected button */
  .button-style.selected {
    background-color: #9b9b9b;
  }
  
  /* Add a style for when button is clicked */
  .button-style:active {
    transform: scale(1.05); /* Enlarge the button slightly on click */
  }
  
  .box-style {
    background-color: #555; /* Darker grey box background */
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
    width: 300px;
  }
  
  .selected-fight-content {
    background-color: #666; /* Darker grey selected fight content background */
    padding: 10px;
    border-radius: 5px;
    margin-top: 10px;
  }
  
  /* Health bar colors */
  .progress-bar-container {
    width: 100%;
    height: 10px; /* Adjust the height as needed */
    background-color: transparent;
    border-radius: 5px;
    overflow: hidden; /* Ensure the color bar doesn't overflow */
  }
  
  .health-bar {
    height: 100%;
    border-radius: 5px;
  }
  
  .health-bar-color-pink {
    background-color: rgb(255, 139, 177);
  }
  
  .health-bar-color-orange {
    background-color: orange;
  }
  
  .health-bar-color-purple {
    background-color: rgb(163, 23, 163);
  }
  
  .health-bar-color-blue {
    background-color: rgb(0, 89, 255);
  }
  
  .health-bar-color-green {
    background-color: rgb(9, 182, 9);
  }
  
  .health-bar-color-grey {
    background-color: grey;
  }
  .health-bar-color-killed {
    background-color: rgb(255, 222, 173);
  }
  .fight-container {
    display: flex;
    align-items: flex-start;
    margin-bottom: 10px;
  }
  
  /* Add styles for selected button */
  .selected {
    background-color: lightgray; /* Change this to your preferred selected button style */
  }
  
  /* Add styles for selected-fight-content */
  .selected-fight-content {
    margin-left: 10px;
    flex-grow: 1;
  }

  .player-box {
    border: 1px solid #777;
    padding: 10px;
    border-radius: 8px;
    cursor: pointer;
    margin: 5px 0;
    transition: background-color 0.3s;
  }
  
  .player-box.expanded {
    background-color: #555;
  }
  
  .player-dropdown {
    background-color: #666;
    padding: 10px;
    margin: 5px 0;
  }

  .home-page-container {
    text-align: center;
    padding: 20px;
  }
  
  .home-page-title {
    font-size: 48px;
  }
  .home-page-title .orange-text {
    color: orange;
    font-weight: bold;
    margin-left: 10px;
  }
  
  .input-field {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 400px;
    margin-right: 10px;
  }
  
  .submit-button {
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }

  .navbar {
    background-color: #333;
    padding: 10px 20px;
    display: flex;
    align-items: center;
  }
  
  .navbar-title {
    color: white;
    text-decoration: none;
    font-size: 27px;
    display: flex;
    align-items: center;
  }
  
  .navbar-title .orange-text {
    color: orange;
    font-weight: bold;
  }
  
  .search-bar {
    display: flex;
    align-items: center;
    margin-left: 50px;
  }
  
  .search-input {
    padding: 5px;
    border: 1px solid #ccc;
    height: 19px;
    width: 450px;
    border-radius: 4px;
    margin-right: 10px;
  }
  
  .search-button {
    padding: 5px 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }

  .navbar-links {
    display: flex;
    align-items: center;
    gap: 20px;
    color: white; 
    margin-left:auto
  }

  .navbar-link {
    text-decoration: none;
    font-weight: bold;
    font-size: 20px;
    color: white;
  }

  .navbar-link:visited {
    color: white; /* Set the color to white for visited links */
  }
  
  .navbar-link:active {
    color: white; /* Set the color to white for active links */
  }

  .about-page-container {
    background-color: #333;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .about-box {
    background-color: #444;
    border-radius: 10px;
    padding: 20px;
    max-width: 600px;
    color: white;
    display: flex; /* Use flexbox to stack text vertically */
    flex-direction: column; /* Stack elements vertically */
    gap: 15px; /* Add some spacing between stacked elements */
  }
  
  .about_head {
    text-align: center;
  }